import * as React from 'react';
import { ViewState, AppointmentModel } from '@devexpress/dx-react-scheduler';
import Container from '@material-ui/core/Container';
import {
  Scheduler,
  WeekView,
  Toolbar,
  DateNavigator,
  ViewSwitcher,
  Appointments,
  TodayButton,
  MonthView,
  DayView,
  CurrentTimeIndicator,
  AppointmentTooltip,
  AppointmentForm,
} from '@devexpress/dx-react-scheduler-material-ui';
import { appointments } from 'routes/Calendar/data';

export type CalState = {
  data: AppointmentModel[];
  currentDate: string;
};

export class Calendar extends React.PureComponent<{}, CalState> {
  state = {
    data: appointments,
    currentDate: new Date().toDateString(),
  } as Readonly<CalState>;

  currentDateChange = (currentDate: Date) => {
    this.setState({ currentDate: currentDate.toDateString() });
  };

  render() {
    const { data, currentDate } = this.state;

    return (
      <Container maxWidth="lg">
        <Scheduler data={data} height={660}>
          <ViewState
            currentDate={currentDate}
            onCurrentDateChange={this.currentDateChange}
            defaultCurrentViewName="Week"
          />
          <WeekView startDayHour={9} endDayHour={19} />
          <WeekView name="work-week" displayName="Work Week" excludedDays={[0, 6]} startDayHour={9} endDayHour={19} />
          <MonthView />
          <DayView />
          <Toolbar />
          <DateNavigator />
          <ViewSwitcher />
          <TodayButton />
          <Appointments />
          <CurrentTimeIndicator shadePreviousCells={false} shadePreviousAppointments={true} updateInterval={60000} />
          <AppointmentTooltip showCloseButton showOpenButton />
          <AppointmentForm />
        </Scheduler>
      </Container>
    );
  }
}

export default Calendar;
